.capPost{
    display: flex;
    justify-content: end;
}
.postBlock{
    display: flex;
    flex-direction: column;
    /*align-items: center;*/
    height: 500px;
    width: 550px;
}

.addButton{
    border: none;
    background: none;
    cursor: pointer;
}

.addIcon img{
    height: 18px;
    width: 18px;
}

.textAreaBlock{
    display: flex;
    height: 100%;
    width: 600px;
    overflow-y: scroll;
    box-sizing: border-box;
    flex-direction: column;
}

.postTextarea{
    /*width: 500px;*/
    height: 50px;
   resize: none;
}
.textPost{
    display: flex;

}
